<template>
  <div class="login">
    <div class="first_img">
      <img src="../assets/image/hua.png" />
    </div>
    <div class="login_body">
      <div class="login_body_m">
        <div class="box">
          <div class="box_t">
            <i class="iconfont icon-renxiang"></i>
            <input type="text" placeholder="昵称/用户名" v-model="loginUser.username" />
          </div>
          <div class="box_b"></div>
        </div>
        <div class="box">
          <div class="box_t">
            <i class="iconfont icon-mima1"></i>
            <input type="password" placeholder="密码" v-model="loginUser.password" />
          </div>
          <div class="box_b"></div>
        </div>
        <div class="deng" @click="login">登录</div>
      </div>
      <div class="login_body_b">
        <i @click="reg">还没有账号？去注册</i>
      </div>
    </div>
    <div class="login_bodyy"></div>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      loginUser: {
        username: "",
        password: ""
      }
    };
  },
  created() {},
  methods: {
    error1() {
      this.$message({
        showClose: true,
        message: "您输入的账号或者密码有误",
        type: "error",
        offset: 280
      });
    },
    login() {
      let username = this.loginUser.username;
      let password = this.loginUser.password;
      this.$axios({
        url: "https://tang1.top/qimimi/LoginUser/By/Nike_NameAndPassword",
        method: "post",
        params: {
          username: username,
          password: password
        }
      }).then(res => {
        if (res.status == 200) {
          localStorage.setItem("username", this.loginUser.username);
          localStorage.setItem("userid", res.data.id);
          localStorage.setItem("faculty", JSON.stringify(res.data.faculty));
          localStorage.setItem("school", JSON.stringify(res.data.school));
          this.$router.push("/index");
        } else {
          // 密码或者账号输入有误
          this.error1();
        }
      });
    },
    reg() {
      this.$router.push("/reg");
    }
  }
};
</script>
<style lang="scss" scoped>
.login {
  width: 100%;
  > .first_img {
    width: 100%;
    position: relative;
    img {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  > .login_body {
    width: 100%;
    height: 99%;
    position: fixed;
    top: 0;
    // z-index: 2;
      background: rgba($color: #000000, $alpha: 0.55);
    > .login_body_m {
      width: 100%;
      position: absolute;
      top: 6.5rem;
      box-sizing: border-box;
      > .box {
        width: 100%;
        box-sizing: border-box;
        margin-bottom: 0.35rem;
        position: relative;
        > .box_t {
          display: flex;
          padding-left: 0.3rem;
          width: 100%;
          box-sizing: border-box;
          > i {
            font-size: 0.7rem;
            color: rgba($color: #fff, $alpha: 0.6);
            margin-right: 0.25rem;
          }
          > input {
            background: none;
            font-size: 0.4rem;
            color: rgba($color: #fff, $alpha: 0.6);
          }
        }
        > .box_b {
          margin-top: 0.2rem;
          border-bottom: 0.03rem solid rgba($color: #fff, $alpha: 0.6);
        }
      }
      > .deng {
        width: 90%;
        margin: 0 auto;
        height: 1rem;
        line-height: 1rem;
        background: rgba(202, 171, 103, 0.603);
        color: rgba($color: #fff, $alpha: 0.6);
        font-size: 0.4rem;
        border-radius: 0.3rem;
        margin-top: 0.8rem;
      }
    }
    > .login_body_b {
      width: 100%;
      position: absolute;
      bottom: 0.5rem;
      box-sizing: border-box;
      > i {
        font-style: normal;
        font-size: 0.3rem;
        color: rgba($color: #fff, $alpha: 0.6);
      }
    }
  }
  > .login_bodyy {
    width: 100%;
    height: 1%;
    position: fixed;
    bottom: 0;
     background: rgba($color: #000000, $alpha: 0.55);
    // background: rgba($color: #662727, $alpha: 0.55);
  }
}
</style>