<template>
  <div class="reg">
    <div class="first_img">
      <img src="../assets/image/hua.png" />
    </div>
    <div class="reg_body">
      <div class="reg_body_t" @click="to_login">
        <i class="iconfont icon-l-back"></i>
      </div>
      <div class="reg_body_m">注 册</div>
      <div class="reg_body_b">
        <div class="box">
          <div class="box_t">
            <i class="iconfont icon-renxiang"></i>
            <input
              type="text"
              placeholder="昵称/用户名"
              v-model="nickNamereg"
              @blur="check_input1"
              class="span_1"
            />
          </div>
          <div class="box_b"></div>
          <div class="span1_info hide">
            <span>昵称不能为空</span>
          </div>
        </div>
        <div class="box">
          <div class="box_t">
            <i class="iconfont icon-mima1"></i>

            <input
              type="password"
              placeholder="密码"
              v-model="passwordreg"
              @blur="check_input2"
              class="span_2"
            />
          </div>
          <div class="box_b"></div>
          <div class="span2_info hide">
            <span>密码至少六位</span>
          </div>
        </div>
        <div class="box">
          <div class="box_t">
            <i class="iconfont icon-mail-copy"></i>
            <input
              type="email"
              placeholder="邮箱"
              v-model="emailreg"
              @blur="check_input3"
              class="span_3"
            />
          </div>
          <div class="box_rsg" @click="sendmsg">
            <i v-show="show">获取验证码</i>
            <i v-show="!show" class="count">{{count}}s</i>
          </div>
          <div class="box_b"></div>
          <div class="span3_info hide">
            <span>邮箱格式不正确</span>
          </div>
        </div>
        <div class="box">
          <div class="box_t">
            <i class="iconfont icon-mima"></i>
            <input type="text" placeholder="邮箱验证码" v-model="emailmsg" />
          </div>
          <div class="box_b"></div>
        </div>
        <div class="zhu" @click="regg">注册</div>
      </div>
    </div>
    <div class="reg_bodyy"></div>
  </div>
</template>
<script>
export default {
  name: "reg",
  data() {
    return {
      nickNamereg: "",
      passwordreg: "",
      emailreg: "",
      emailmsg: "",
      show: true,
      count: "",
      timer: null
    };
  },
  created() {},
  methods: {
    error1() {
      this.$message({
        showClose: true,
        message: "您还有内容未填完",
        type: "error",
        offset: 120
      });
    },
    error2() {
      this.$message({
        showClose: true,
        message: "验证码错误，请重新填写验证码",
        type: "error",
        offset: 120
      });
    },
    //邮箱的正则校验
    validateEmail(email) {
      // 邮箱验证正则
      var reg = /^[A-Za-z0-9]+([_\.][A-Za-z0-9]+)*@([A-Za-z0-9\-]+\.)+[A-Za-z]{2,6}$/;
      return reg.test(email);
    },
    check_input1() {
      if (!this.nickNamereg) {
        document.getElementsByClassName("span_1")[0].classList.add("error");
        document
          .getElementsByClassName("span1_info")[0]
          .classList.remove("hide");
      } else {
        document.getElementsByClassName("span_1")[0].classList.remove("error");
        document.getElementsByClassName("span1_info")[0].classList.add("hide");
      }
    },
    check_input2() {
      if (this.passwordreg.length < 6) {
        document.getElementsByClassName("span_2")[0].classList.add("error");
        document
          .getElementsByClassName("span2_info")[0]
          .classList.remove("hide");
      } else {
        document.getElementsByClassName("span_2")[0].classList.remove("error");
        document.getElementsByClassName("span2_info")[0].classList.add("hide");
      }
    },
    check_input3() {
      let d = this.validateEmail(this.emailreg);
      if (d) {
        document.getElementsByClassName("span_3")[0].classList.remove("error");
        document.getElementsByClassName("span3_info")[0].classList.add("hide");
      } else {
        document.getElementsByClassName("span_3")[0].classList.add("error");
        document
          .getElementsByClassName("span3_info")[0]
          .classList.remove("hide");
      }
    },
    to_login() {
      this.$router.push("/login");
    },
    sendmsg() {
      let aa = this.nickNamereg;
      let bb = Boolean(this.passwordreg.length > 5);
      let dd = this.validateEmail(this.emailreg);
      if (aa && bb && dd) {
        const TIME_COUNT = 60;
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000);
        }
        this.$axios({
          url: "https://tang1.top/qimimi/Register/User",
          method: "post",
          data: {
            email: this.emailreg,
            nickName: this.nickNamereg,
            password: this.passwordreg
          }
        }).then(res => {
          console.log(res);
        });
      } else {
        //有内容没填完
        this.error1();
      }
    },
    regg() {
      this.$axios({
        url: "https://tang1.top/qimimi/Register/User?random=" + this.emailmsg,
        method: "post",
        data: {
          email: this.emailreg,
          nickName: this.nickNamereg,
          password: this.passwordreg
        }
      }).then(res => {
        if (res.data == "注册失败，激活码不对") {
          this.error2();
        } else {
          this.to_login();
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.reg {
  width: 100%;
  .error {
    border: 1px solid rgba(180, 21, 21, 0.658) !important;
    border-radius: 8px;
  }
  .hide {
    display: none;
  }
  .first_img {
    width: 100%;
    position: relative;
    img {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  > .reg_body {
    width: 100%;
    height: 99%;
    position: fixed;
    top: 0;
    // z-index: 2;
    background: rgba($color: #000000, $alpha: 0.55);
    > .reg_body_t {
      position: absolute;
      top: 1rem;
      left: 0.4rem;
      i {
        font-size: 0.8rem;
        color: rgba($color: #fff, $alpha: 0.6);
      }
    }
    > .reg_body_m {
      position: absolute;
      top: 2.5rem;
      left: 0.45rem;
      font-size: 0.6rem;
      color: rgba($color: #fff, $alpha: 0.6);
    }
    > .reg_body_b {
      width: 100%;
      position: absolute;
      top: 4rem;
      box-sizing: border-box;
      > .box {
        width: 100%;
        box-sizing: border-box;
        margin-bottom: 0.35rem;
        position: relative;
        > .box_t {
          display: flex;
          padding-left: 0.3rem;
          width: 100%;
          box-sizing: border-box;
          > i {
            font-size: 0.7rem;
            color: rgba($color: #fff, $alpha: 0.6);
            margin-right: 0.25rem;
          }
          > input {
            background: none;
            font-size: 0.4rem;
            color: rgba($color: #fff, $alpha: 0.6);
          }
        }
        > .box_rsg {
          position: absolute;
          top: 0;
          right: 0.2rem;
          width: 2rem;
          height: 0.8rem;
          line-height: 0.8rem;
          background: rgba($color: #fff, $alpha: 0.5);
          background: rgba(202, 171, 103, 0.603);

          border-radius: 0.3rem;
          > i {
            font-style: normal;
            font-size: 0.3rem;
          }
        }
        .box_b {
          margin-top: 0.2rem;
          border-bottom: 0.03rem solid rgba($color: #fff, $alpha: 0.6);
        }
        > .span1_info {
          position: absolute;
          top: -0.4rem;
          left: 2rem;
          color: rgba($color: rgb(248, 240, 240), $alpha: 0.6);
          font-size: 0.3rem;
        }
        > .span2_info {
          position: absolute;
          top: -0.4rem;
          left: 2rem;
          color: rgba($color: rgb(248, 240, 240), $alpha: 0.6);
          font-size: 0.3rem;
        }
        > .span3_info {
          position: absolute;
          top: -0.4rem;
          left: 2rem;
          color: rgba($color: rgb(248, 240, 240), $alpha: 0.6);
          font-size: 0.3rem;
        }
      }
      > .zhu {
        width: 90%;
        margin: 0 auto;
        height: 1rem;
        line-height: 1rem;
        background: rgba(202, 171, 103, 0.603);
        color: rgba($color: #fff, $alpha: 0.6);
        font-size: 0.4rem;
        border-radius: 0.3rem;
        margin-top: 0.8rem;
      }
    }
  }
  > .reg_bodyy {
    width: 100%;
    height: 1%;
    position: fixed;
    bottom: 0;
    background: rgba($color: #000000, $alpha: 0.55);
  }
}
</style>
